import React from 'react';
import Category from './category';
import Header from '../useradmin/header';
import Nav from '../useradmin/nav';
import { withRouter } from 'react-router-dom'
import { Route, Switch } from 'react-router-dom';
import { withGoogleMap, GoogleMap, withScriptjs, Marker,Autocom } from "react-google-maps";
import {isMobile} from 'react-device-detect';
import Geocode from "react-geocode";
import Map from "./multimarker";

const axios = require('axios');
var dateFormat = require('dateformat');

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.isPay=false
    this.clickType = this.clickType.bind(this);
    this.mapRefresh = this.mapRefresh.bind(this);
    this.input = React.createRef();
    this.input1 = React.createRef();
    this.state={
      nList:0,
      data:[],
      amount:0,
      isCOP: false,
      isCOD: false,
      isOnline: false,
      tempAmt:0,
      tempId:'',tempRefno:'',
      isRefresh:false,isNew:false,tempOTP:'',
      tempDrivername:'',tempMobile:'',tempVehicle:'',tempPickup:0,
      tempLat:0,tempLng:0
    }
  }

  orderlist(cond)
  {
    axios.get(global.serverURL + "/api/courier/myorders", {
      params: {
        "mobno": global.mobno===''? "9095204586":global.mobno
      }
    })
    .then((response) => {
      console.log(response)
      return response.data.data;
    })
    .then(data => {
      this.setState({ nList: data.length, data:data, isRefresh:!this.state.isRefresh,tempLat:data[0].dlatitude,tempLng:data[0].dlongitude });
      console.log(data[0].dlatitude+','+data[0].dlongitude)
    }).catch(error => {
      this.setState({ nList: 0 });      
      console.log(error);
    });
  }

  onlinePay=(data)=>
  {
    this.setState({tempPickup:data.driver1.isPickup,tempAmt:data.driver1.NetTotal,tempId:data.driver1.BookingId,tempOTP:(data.driver1.isPickup==0?data.driver1.otp:data.driver1.recotp)});
  }

  driverinfo=(data)=>
  {
    this.setState({tempDrivername:data.driver1.driver_name,tempMobile:data.driver1.Drivermobile,tempVehicle:data.driver1.vehicle_number});
  }
  componentDidMount()
  {
    this.orderlist();    
    //window.location.reload();
  }
  mapRefresh()
  {
    this.orderlist();
    //window.location.reload();
  }

  shouldComponentUpdate( nextProps, nextState ){
    return true;
  }

  clickType=(type,res)=>{
    if(type==='cancelbooking')
    {
      let conf=window.confirm('Are u want sure to Cancel Booking?');
      if(conf) 
      {
        console.log(res.id)
        axios.post(global.serverURL + "/api/driver/BookingAction",  {
          "id": res.id1,"action":"reject"
        }
        )
        .then((response) => {
          console.log(response)
          if(response.status===200)
          {
            this.orderlist();
          }
          return response.data;
        }).catch(error => {
          console.log(error);
        });
      }
//else alert('false');
}
else if(type==='online')
{
  this.openCheckout();
}
else if(type==='new')
{
  this.setState({isNew:true});
}
}

openCheckout=()=> {
  let id1=this.state.tempId;
  let options = {
    "key": "rzp_test_zfgh53To0jzwp9",
"amount": this.state.tempAmt*100, // 2000 paise = INR 20, amount in paisa
"name": "Donkeycargo Pvt Ltd.,",
"description": "Online Shipping System",
"image": "./dist/img/DonkeyUser.png",
"handler": (response)=> {
  alert(response.razorpay_payment_id);

  axios.post(global.serverURL + "/api/courier/updatePaymode",  {
    "id": id1,"paymode":"online","refno":response.razorpay_payment_id
  }
  )
  .then((response) => {
    this.setState({isRefresh:true});
    this.orderlist();
    return response.data.data;
  }).catch(error => {
    console.log(error);
  });

},
"prefill": {
  "name": "Karthic",
  "email": "karthi@gmail.com"
},
"notes": {
  "address": "Hello World"
},
"theme": {
  "color": "#F37254"
}
};

let rzp = new window.Razorpay(options);
rzp.open();
}

getsidebar(e){
  console.log(e)
}

render()
{
  let res;
  let res1;
  const places1 = [{
    "id": 1,            
    "name": "Maduraii",
    "latitude": this.state.tempLat,
    "longitude": this.state.tempLng
  }];

  let lat1=parseFloat(this.state.tempLat,10)
  let lng1=parseFloat(this.state.tempLng,10)

  const buttonstylefocus={
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontWeight: '700',
    border: '1px solid #f4511e',
    color: '#fff',
    background: '#f4511e',
    boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)',
    wordWrap: 'break-word'
  };
  const buttonstyle={
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    color: '#f4511e',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    border: '1px solid #f4511e',
    fontWeight: '700',
    textDecoration:'none',
    background: 'transparent',
    wordWrap: 'break-word'
  };
  const styleForm={
    paddingLeft:'10px',
    paddingBottom:'5px'
  }
  let orders=[]
  let i=0;
  for(i=0;i<this.state.nList;i++)
  {
    let booktime=dateFormat(this.state.data[i].BookDate, "dddd, mmm dS, yyyy h:MM:ss TT",true);
      let amt1=this.state.data[i].NetTotal
      let id1=this.state.data[i].id
      let id=this.state.data[i].BookingId
      let otp=this.state.data[i].otp
      let driver1=this.state.data[i]
      let pickup=this.state.data[i].isPickup
      let dropotp=this.state.data[i].recotp
      const viewpickup=<div>View Pickup&nbsp;OTP</div>
      const viewdrop=<div>View Drop&nbsp;OTP</div>

      orders.push(

        <div class="card card-default">
        <div style={{marginBottom:'10px'}}>
        <div class="callout callout-danger">
        <div className="row">
        <div className="col-9">
        <h3>Order #{this.state.data[i].BookingId}</h3>    
        </div>
        <div className="col-3" >
        {this.state.data[i].Refno===''?<button className="btn btn-sm btn-danger" readOnly>Unpaid</button>:<button className="btn btn-sm btn-success" readOnly>Paid</button>} 
        </div>  

        <div className="col-12 mb-2">
        <p>Placed on {booktime}</p>
        </div>             

        <div className="col-12">
        <div style={styleForm}>
        <label className="select2text">From</label>
        <div className="input-group">
        <span class="input-group-text text-success"><i class="fa fa-dot-circle-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].FromAddress} />
        </div>
        </div>
        </div>

        <div className="col-12">
        <div style={styleForm}>
        <label className="select2text">To</label>
        <div className="input-group">
        <span class="input-group-text text-danger"><i class="fa fa-dot-circle-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].toaddress} />
        </div>
        </div>
        </div>

        <div className="col-7">
        <div style={styleForm}>
        <label className="select2text">Courier Type</label>
        <div className="input-group mb-3">
        <span class="input-group-text text-primary"><i class="fa fa-envelope-o"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].Type} />
        </div>
        </div>
        </div>
        <div className="col-5 mb-2">
        <div style={styleForm}>
        <label className="select2text">Amount</label>
        <div className="input-group mb-3">
        <span class="input-group-text bg-danger"><i class="fa fa-inr"></i></span>
        <div className="input-group-prepend">
        </div>
        <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.data[i].NetTotal} />
        </div>
        </div>
        </div>

        <div className="col-6 col-md-3 mb-2 text-center" disabled={this.state.data[i].isPickup==0?false:true}>
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" onClick={()=>this.clickType('cancelbooking',{id1})}>Cancel Booking</a>
        </div>
        <div className="col-6 col-md-3 text-center" disabled={this.state.data[i].isPickup==0?false:true}>
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal2" onClick={()=>this.onlinePay({driver1})}>Setup Payment</a>
        </div>
        <div className="col-6 col-md-3 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#myModal3" onClick={()=>this.onlinePay({driver1})}>{this.state.data[i].isPickup==0?viewpickup:viewdrop}</a>
        </div>
        <div className="col-6 col-md-3 text-center">
        <a href="#" style={this.state.isLocal?buttonstylefocus:buttonstyle} className="orange-back-link" data-toggle="modal" data-target="#driverModal" onClick={()=>this.driverinfo({driver1})}>Driver Info</a>
        </div>

        </div>

        </div>

        </div>
        </div>
        );
      }   

      res1 = <div className="">
      <div className="row">      
      <div className="col-12">
      {orders}        

      </div>

      </div>

      </div>
      ;

      let cls='is-active'
      let body1='You have '+this.state.nList+' New Order(s)'
      const disp=<span>Pickup OTP</span>
      const disp1=<span>Drop OTP</span>
      
      res = <div className="wrapper">
      <Header render={res1} panelheader='My Orders' panelbody={body1} />

      <div class="modal fade" id="driverModal" tabindex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div className="modal-dialog  modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-body" style={{padding:'0.5rem'}}>
      <div className="row">
      <div className="col-12 text-center mb-2">

      <div class="card card-primary card-outline">
      <div class="card-body box-profile">
      <div class="">
      <img class="profile-user-img img-fluid img-circle"
      src="dist/img/avatar5.png"
      alt="User profile picture" />
      </div>

      <h3 class="profile-username text-center">Mr. {this.state.tempDrivername}</h3>

      <p class="text-muted text-center">Madmoney Driver</p>

      <ul class="list-group list-group-unbordered mb-3 text-left">
      <li class="list-group-item">
      <b>Mobile No.</b> <a class="float-right">{this.state.tempMobile}</a>
      </li>
      <li class="list-group-item">
      <b>Vehicle No.</b> <a class="float-right">{this.state.tempVehicle}</a>
      </li>
      <li class="list-group-item">
      <b>Rating</b> 
      <a class="float-right">
      <div>
      <i class="fa fa-star"></i>
      <i class="fa fa-star"></i>
      <i class="fa fa-star"></i>
      <i class="fa fa-star-half-o"></i>
      <i class="fa fa-star-o"></i>
      </div>  
      </a>
      </li>
      </ul>

      </div>
      </div>

      </div>
      </div>
      <div className="modal-footer" style={{padding:'0.5rem'}}>
      <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#mapModal">Where's Driver?</button>
      <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>

      </div>
      </div>

      <div class="modal fade" id="mapModal" tabindex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div className="modal-dialog modal-lg modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-body" style={{padding:'0.5rem'}}>
      <div className="row">
      <div className="col-12 text-center mb-2">

      <div class="">
      <div class="">

      <Map
      center={{lat: lat1, lng: lng1}}
  zoom={11}
  places={places1}
  height='600px'
  />
    
      </div>
      </div>

      </div>
      </div>
      <div className="modal-footer" style={{padding:'0.5rem'}}>
      <button type="button" className="btn btn-primary" onClick={this.mapRefresh}>Refresh</button>
      <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>

      </div>
      </div>


      <div id="myModal2" tabindex="-1" className="modal fade" role="dialog">
      <div className="modal-dialog  modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-header">
      <h4 className="modal-title">Setup Payment</h4>
      <button type="button" className="close" data-dismiss="modal">&times;</button>
      </div>
      <div className="modal-body">
      <div className="row">
      <div className="col-12">
      <a href="#" style={this.state.isCOD ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cod")} data-dismiss="modal">
      <span style={{ display: 'grid' }}><i className="fa fa-truck fa-flip-horizontal fa-3x"></i>
      Cash on Delivery</span></a>
      </div>

      <div className="col-12">
      <a href="#" style={this.state.isOnline ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("online")} data-dismiss="modal">
      <span style={{ display: 'grid' }} data-dismiss="modal"><i className="fa fa-credit-card fa-3x"></i>
      Online Payment</span></a>
      </div>

      </div>
      <div className="modal-footer">
      <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>
      </div>
      </div>

      <div id="myModal3" tabindex="-1" className="modal fade" role="dialog">
      <div className="modal-dialog  modal-dialog-centered">

      <div className="modal-content">
      <div className="modal-header">
      <h4 className="modal-title">{this.state.tempPickup==0?disp:disp1}</h4>
      <button type="button" className="close" data-dismiss="modal">&times;</button>
      </div>
      <div className="modal-body">
      <div className="row">
      <div className="col-12 text-center mb-2">
      {this.state.tempPickup==0?disp:disp1} is <span class="bg-danger">{this.state.tempOTP}</span>
      </div>
      </div>
      <div className="modal-footer">
      <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
      </div>
      </div>

      </div>
      </div>
      </div>

      </div>
      
      ;


      if(this.state.isNew)
      {
        return <Category />
      }
      else    return (res);
    }
  }

  export default withRouter(App1);
